<template>
  <form v-bind="$attrs" class="filter__form form" action="#" method="get" @submit.prevent="submit">
    <fieldset class="form__block">
      <legend class="form__legend">Цена</legend>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom" @focus="focusPriceFrom"
          @blur="blurPriceFrom">
        <span class="form__value">От</span>
      </label>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo" @focus="focusPriceTo"
          @blur="blurPriceTo">
        <span class="form__value">До</span>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Категория</legend>
      <label class="form__label form__label--select">
        <select class="form__select" name="category" v-model.number="currentCategoryId">
          <option value="0">Все категории</option>
          <option v-for="category in categories" :key="category.id" :value="category.id">
            {{ category.title }}
          </option>
        </select>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Цвет</legend>
      <ul class="colors">
        <li class="colors__item" v-for="color in colors" :key="color.id">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="checkbox" name="color" v-model.number="currentColorIds"
              :value="color.id">
            <span class="colors__value" :style="{ backgroundColor: color.code }">
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Материал</legend>
      <ProductCheckList :items="materials" :checked-items.sync="currentMaterialIds" />
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Коллекция</legend>
      <ProductCheckList :items="seasons" :checked-items.sync="currentSeasonIds" />
    </fieldset>

    <button class="filter__submit button button--primery" type="submit">
      Применить
    </button>
    <button class="filter__reset button button--second" type="button" @click="reset" v-if="showButtonReset">
      Сбросить
    </button>
  </form>
</template>

<script>
import ProductCheckList from '@/components/ProductCheckList.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config.js';

export default {
  components: { ProductCheckList },
  props: [
    'priceFrom',
    'priceTo',
    'categoryId',
    'colorIds',
    'materialIds',
    'seasonIds',
    'selectCategoryId',
  ],
  data() {
    return {
      categoriesData: null,
      colorsData: null,
      materialsData: null,
      seasonsData: null,

      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: this.selectCategoryId ? this.selectCategoryId : 0,
      currentColorIds: [],
      currentMaterialIds: [],
      currentSeasonIds: [],
    }
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData : [];
    },
    colors() {
      return this.colorsData ? this.colorsData : [];
    },
    materials() {
      return this.materialsData ? this.materialsData : [];
    },
    seasons() {
      return this.seasonsData ? this.seasonsData : [];
    },
    filters() {
      return {
        currentPriceFrom: this.currentPriceFrom,
        currentPriceTo: this.currentPriceTo,
        currentCategoryId: this.currentCategoryId,
        currentColorIds: this.currentColorIds,
        currentMaterialIds: this.currentMaterialIds,
        currentSeasonIds: this.currentSeasonIds,
      };
    },
    showButtonReset() {
      const initial = {
        currentPriceFrom: 0,
        currentPriceTo: 0,
        currentCategoryId: 0,
        currentColorIds: [],
        currentMaterialIds: [],
        currentSeasonIds: [],
      };
      return JSON.stringify(initial) !== JSON.stringify(this.filters);
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    colorIds(value) {
      this.currentColorIds = value;
    },
    materialIds(value) {
      this.currentMaterialIds = value;
    },
    seasonIds(value) {
      this.currentSeasonIds = value;
    },
  },
  methods: {
    loadFilters() {
      Promise.all([
        axios.get(API_BASE_URL + '/api/productCategories'),
        axios.get(API_BASE_URL + '/api/colors'),
        axios.get(API_BASE_URL + '/api/materials'),
        axios.get(API_BASE_URL + '/api/seasons'),
      ])
        .then((response) => {
          this.categoriesData = response[0].data.items;
          this.colorsData = response[1].data.items;
          this.materialsData = response[2].data.items;
          this.seasonsData = response[3].data.items;
        })
        .catch(() => {
          console.log('Ошибка загрузки фильтров');
        });
    },
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:colorIds', this.currentColorIds);
      this.$emit('update:materialIds', this.currentMaterialIds);
      this.$emit('update:seasonIds', this.currentSeasonIds);
    },
    reset() {
      this.$emit('update:priceFrom', (this.currentPriceFrom = 0));
      this.$emit('update:priceTo', (this.currentPriceTo = 0));
      this.$emit('update:categoryId', (this.currentCategoryId = 0));
      this.$emit('update:colorIds', (this.currentColorIds = []));
      this.$emit('update:materialIds', (this.currentMaterialIds = []));
      this.$emit('update:seasonIds', (this.currentSeasonIds = []));
    },
    focusPriceFrom() {
      this.focused = true;
      if (this.currentPriceFrom === 0) this.currentPriceFrom = '';
    },
    blurPriceFrom() {
      this.focused = false;
      if (this.currentPriceFrom === '') this.currentPriceFrom = 0;
    },
    focusPriceTo() {
      this.focused = true;
      if (this.currentPriceTo === 0) this.currentPriceTo = '';
    },
    blurPriceTo() {
      this.focused = false;
      if (this.currentPriceTo === '') this.currentPriceTo = 0;
    },
  },
  created() {
    this.loadFilters();
  },
}
</script>

<style>
.colors__label:focus .colors__value::before,
.colors__label:hover .colors__value::before,
.colors__radio:checked~.colors__value::before {
  border-color: #222;
}
</style>