<template>
  <ul class="cart__options options">
    <li class="options__item" v-for="delivery in deliveries" :key="delivery.id">
      <label class="options__label">
        <input class="options__radio sr-only" type="radio" name="delivery" :value="delivery.id"
          v-model="computedDelivery" />

        <span class="options__value">
          {{ delivery.title }} <b>{{ delivery.price | numberFormat }} ₽</b>
        </span>
      </label>
    </li>
  </ul>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  props: [
    'deliveries',
    'currentDelivery',
    'deliveryPrice',
  ],
  filters: { numberFormat },
  computed: {
    computedDelivery: {
      get() {
        return this.currentDelivery;
      },
      set(value) {
        this.$emit('update:currentDelivery', value);
        this.$emit('update:deliveryPrice', Number(this.deliveries.find(delivery => delivery.id === value).price));
      },
    },
  },
};
</script>