<template>
  <ul class="pics__list">
    <li class="pics__item" v-for="image in images" :key="image.name">
      <a href="#" @click.prevent="selectImage(image)">
        <img width="98" height="98" class="pics__link" :alt="image.gallery ? image.gallery[0].file.url : notFoundPhoto"
          :src="image.gallery ? image.gallery[0].file.url : notFoundPhoto" :class="{
            'pics__link--current':
              currentImage === (image.gallery ? image.gallery[0].file.url : notFoundPhoto),
          }">
      </a>
    </li>
  </ul>
</template>

<script>
import notFoundPhoto from '@/assets/img/not-found-photo.png';

export default {
  props: ['images', 'currentImage'],
  data() {
    return {
      notFoundPhoto,
    };
  },
  methods: {
    selectImage(value) {
      this.$emit('update:currentImage', value);
    },
  },
}
</script>