<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <BasePreloader v-if="cartLoading" />

      <form v-if="!cartLoading" class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО" placeholder="Введите ваше полное имя" v-model="formData.name"
              :error="formError.name" />
            <BaseFormText title="Адрес доставки" placeholder="Введите ваш адрес" v-model="formData.address"
              :error="formError.address" />
            <BaseFormText title="Телефон" placeholder="Введите ваш телефон" v-model="formData.phone"
              :error="formError.phone" />
            <BaseFormText title="Email" placeholder="Введите ваш Email" v-model="formData.email"
              :error="formError.email" />
            <BaseFormTextarea title="Комментарий к заказу" placeholder="Ваши пожелания" v-model="formData.comment"
              :error="formError.comment" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <OrderDelivery :deliveries="deliveries" :currentDelivery.sync="formData.deliveryTypeId"
              :deliveryPrice.sync="deliveryPrice" />

            <h3 class="cart__title">Оплата</h3>
            <OrderPayment :payments="payments" :currentPayment.sync="formData.paymentTypeId" />
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li v-for="item in products" :key="item.id" class="cart__order">
              <div>
                <h3>{{ item.product.title }}</h3>
                <span class="cart__item-size">Размер: {{ item.size.title }}</span>
                <span class="cart__item-color">Цвет: {{ item.color.color.title }}
                  <span class="colors__value colors__value--small"
                    :style="{ 'background-color': item.color.color.code }"></span>
                </span>
              </div>
              <b>{{ item.quantity * item.product.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b> {{ deliveryPrice | numberFormat }} ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ totalPrice + deliveryPrice | numberFormat
            }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery button--preloader" type="submit" :disabled="orderLoading">
            <BasePreloader v-if="orderLoading" class="preloader--button" />
            <span v-else>Оформить заказ</span>
          </button>
        </div>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config.js';
import { mapState, mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import BasePreloader from '@/components/BasePreloader.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import OrderDelivery from '@/components/OrderDelivery.vue';
import OrderPayment from '@/components/OrderPayment.vue';

export default {
  components: { BaseFormText, BaseFormTextarea, BasePreloader, OrderDelivery, OrderPayment },
  filters: { numberFormat },
  data() {
    return {
      deliveriesData: null,
      paymentsData: null,

      formData: {
        name: '',
        address: '',
        phone: '',
        email: '',
        deliveryTypeId: 1,
        paymentTypeId: '',
        comment: '',
      },

      formError: {},
      formErrorMessage: '',

      orderLoading: false,

      deliveryPrice: 0,
    }
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
    }),
    ...mapState(['cartLoading']),
    deliveries() {
      return this.deliveriesData ? this.deliveriesData : [];
    },
    payments() {
      return this.paymentsData ? this.paymentsData : [];
    },
  },
  watch: {
    deliveryPrice: {
      handler() {
        this.changeDelivery();
      },
    },
  },
  created() {
    this.changeDelivery();
    this.loadDeliveries();
  },
  methods: {
    loadDeliveries() {
      axios
        .get(API_BASE_URL + '/api/deliveries')
        .then((response) => this.deliveriesData = response.data);
    },
    loadPayments() {
      axios
        .get(API_BASE_URL + '/api/payments', {
          params: {
            deliveryTypeId: this.formData.deliveryTypeId,
          }
        })
        .then((response) => {
          this.paymentsData = response.data;
          this.formData.paymentTypeId = response.data[0].id
        }
        );
    },
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderLoading = true;

      axios
        .post(API_BASE_URL + '/api/orders', {
          ...this.formData
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey
          }
        })
        .then(response => {
          this.$store.commit('resetCart');
          // this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch(error => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        .finally(() => this.orderLoading = false);
    },
    changeDelivery() {
      this.loadPayments({ deliveryTypeId: this.formData.deliveryTypeId });
    },
  },
};
</script>

<style>
.options__radio:checked~.options__value::before {
  background-color: #222
}

.cart__item-color {
  display: flex;
  align-items: center;
}

.cart__item-size {
  margin-bottom: 5px;
}

.colors__value--small {
  display: inline-block;
  margin-left: 5px;
  width: 10px;
  height: 10px;
}

.button--preloader {
  position: relative;
  height: 67px;
}

.button--preloader:disabled {
  opacity: 1;
}
</style>