<template>
  <ul class="colors colors--black">
    <li class="colors__item" v-for="color in colors" :key="color.id">
      <label class="colors__label">
        <input class="colors__radio sr-only" type="radio" name="color" :value="color" v-model="computedColor"
          :class="{ colors__select: computedColor === color.color.id || JSON.stringify(computedColor) === JSON.stringify(color) }" />
        <span class="colors__value" :style="{ 'background-color': color.color.code }">
        </span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  props: ['colors', 'currentColor'],
  computed: {
    computedColor: {
      get() {
        return this.currentColor;
      },
      set(value) {
        this.$emit('update:currentColor', value);
      },
    },
  },
};
</script>

<style>
.colors__select~.colors__value::before {
  border-color: #222;
}

.colors__value {
  outline: 1px solid #ededed;
}
</style>