<template>
  <ul class="cart__options options">
    <li class="options__item" v-for="payment in payments" :key="payment.id">
      <label class="options__label">
        <input class="options__radio sr-only" type="radio" name="payment" :value="payment.id" v-model="computedPayment" />

        <span class="options__value">
          {{ payment.title }}
        </span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  props: [
    'payments',
    'currentPayment',
  ],
  computed: {
    computedPayment: {
      get() {
        return this.currentPayment;
      },
      set(value) {
        this.$emit('update:currentPayment', value);
      },
    },
  },
};
</script>