<template>
  <main class="content container">
    <BasePreloader v-if="orderLoading" />

    <div v-if="!orderLoading && orderInfo">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
              Корзина
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Оформление заказа
            </a>
          </li>
        </ul>

        <h1 class="content__title">
          Заказ оформлен <span>№ {{ this.$route.params.id }}</span>
        </h1>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">
              Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
              Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
            </p>

            <ul class="dictionary">
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Получатель
                </span>
                <span class="dictionary__value">
                  {{ orderInfo.name }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Адрес доставки
                </span>
                <span class="dictionary__value">
                  {{ orderInfo.address }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Телефон
                </span>
                <span class="dictionary__value">
                  {{ orderInfo.phone }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Email
                </span>
                <span class="dictionary__value">
                  {{ orderInfo.email }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Способ оплаты
                </span>
                <span class="dictionary__value">
                  {{ orderInfo.paymentType }}
                </span>
              </li>
            </ul>
          </div>

          <div class="cart__block">
            <ul class="cart__orders">
              <li v-for="item in orderProducts" :key="item.id" class="cart__order">
                <div class="cart__item-info">
                  <h3>{{ item.product.title }}</h3>
                  <span class="cart__item-size">Размер: {{ item.size.title }}</span>
                  <span class="cart__item-color">Цвет: {{ item.color.color.title }}
                    <span class="colors__value colors__value--small"
                      :style="{ 'background-color': item.color.color.code }"></span>
                  </span>
                </div>
                <b>{{ item.quantity * item.product.price | numberFormat }} ₽</b>
                <span>Артикул: {{ item.id }}</span>
              </li>
            </ul>

            <div class="cart__total">
              <p>Доставка: <b>{{ orderInfo.deliveryType.price | numberFormat }} ₽</b></p>
              <p>Итого: <b>{{ orderProducts.length }}</b> товара на сумму <b>{{ orderInfo.totalPrice |
                numberFormat }} ₽</b></p>
            </div>
          </div>
        </form>
      </section>
    </div>
  </main>
</template>

<script>
import { mapState } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import BasePreloader from '@/components/BasePreloader';

export default {
  components: { BasePreloader },
  computed: {
    ...mapState(['orderLoading', 'orderData']),
    orderProducts() {
      return this.orderInfo.basket.items;
    },
    orderInfo() {
      return this.orderData;
    },
  },
  filters: {
    numberFormat,
  },
  created() {
    if (this.orderInfo && this.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderData', this.$route.params.id);
  },
}
</script>

<style>
.cart__item-info {
  display: flex;
  flex-direction: column;
}
</style>