<template>
  <main class="content container">
    <BasePreloader v-if="productLoading" class="preloader--page" />

    <div v-else-if="productLoadingFailed">
      <h1>Не удалось загрузить товар</h1>
      <button class="button button--primery button--reload" @click.prevent="loadProduct">
        Попробовать еще раз
      </button>
    </div>

    <div v-else>
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link"
              :to="{ name: 'main', params: { selectCategoryId: product.category.id } }">
              {{ product.category.title }}
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              {{ product.title }}
            </a>
          </li>
        </ul>
      </div>

      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img :src="currentImage" width="270" height="350">
          </div>
          <ProductImages :images="product.colors" :currentImage.sync="currentImage" />
        </div>

        <div class="item__info">
          <span class="item__code">Артикул: {{ product.id }}</span>
          <h2 class="item__title">
            {{ product.title }}
          </h2>

          <div class="item__form">
            <form class="form" action="#" method="POST" @submit.prevent="addToCart">
              <div class="item__row item__row--center">
                <BaseCounter class="product__counter" :amount="productAmount" @updateInput="updateInput" />

                <b class="item__price">
                  {{ (product.price * productAmount) | numberFormat }} ₽
                </b>
              </div>

              <div class="item__row">
                <fieldset class="form__block">
                  <legend class="form__legend">Цвет</legend>
                  <ProductColors :colors="product.colors" :currentColor.sync="currentColor" />
                </fieldset>

                <fieldset class="form__block">
                  <legend class="form__legend">Размер</legend>
                  <label class="form__label form__label--small form__label--select">
                    <select class="form__select" type="text" name="size" v-model.number=currentSize>
                      <option value="0">Выберите размер</option>
                      <option :value="size.id" v-for=" size  in  product.sizes " :key="size.id">
                        {{ size.title }}
                      </option>
                    </select>
                  </label>
                </fieldset>
              </div>

              <button class="item__button button button--primery button--preloader" type="submit"
                :disabled="productAddSending">
                <BasePreloader v-if="productAddSending" class="preloader--button" />
                <span v-else>В корзину</span>
              </button>

              <div class="form__messages">
                <p v-show="formError" class="form__message--error">
                  {{ formError }}
                </p>
                <p v-show="productAdded" class="form__message--success">
                  Товар добавлен в корзину!
                </p>
              </div>
            </form>
          </div>
        </div>

        <div class="item__desc">
          <ProductTabs>
            <ProductTab name="Информация о товаре" :selected="true">
              <h3>Состав:</h3>
              <p>
              <ul class="list-reset">
                <li v-for="material in product.materials" :key="material.id">
                  {{ material.title }}
                </li>
              </ul>
              </p>
              <h3>Уход:</h3>
              <p>
                Машинная стирка при макс. 30ºC короткий отжим<br>
                Гладить при макс. 110ºC<br>
                Не использовать машинную сушку<br>
                Отбеливать запрещено<br>
                Не подвергать химчистке<br>
              </p>
            </ProductTab>
            <ProductTab name="Доставка и возврат">
              <h3>Доставка:</h3>
              <p>
                1. Курьерская доставка по Москве и Московской области – 1 200 ₽<br>
                2.Самовывоз из магазина. Список и адреса магазинов Вы можете посмотреть здесь<br>
              </p>
              <h3>Возврат:</h3>
              <p>
                Любой возврат должен быть осуществлен в течение Возвраты в магазине БЕСПЛАТНО.<br>
                Вы можете вернуть товары в любой магазин. Магазин должен быть расположен в стране, в которой Вы
                осуществили
                покупку.
                БЕСПЛАТНЫЙ возврат в Пункт выдачи заказов.<br>
                Для того чтобы вернуть товар в одном из наших Пунктов выдачи заказов, позвоните по телефону 8 800 600 90
                09<br>
              </p>
            </ProductTab>
          </ProductTabs>
        </div>
      </section>
    </div>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config.js';
import { mapActions } from 'vuex';
import numberFormat from '@/helpers/numberFormat.js';
import BasePreloader from '@/components/BasePreloader.vue';
import BaseCounter from '@/components/BaseCounter.vue';
import ProductImages from '@/components/ProductImages.vue';
import ProductColors from '@/components/ProductColors.vue';
import notFoundPhoto from '@/assets/img/not-found-photo.png';
import ProductTabs from '@/components/ProductTabs.vue';
import ProductTab from '@/components/ProductTab.vue';

export default {
  components: { BaseCounter, BasePreloader, ProductImages, ProductTabs, ProductTab, ProductColors },
  props: ['selectColor'],
  data() {
    return {
      productData: null,

      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false,

      formError: '',

      productAmount: 1,

      currentImage: null,
      currentColor: null,
      currentSize: 0,
    }
  },
  filters: { numberFormat },
  computed: {
    product() {
      return this.productData ? this.productData : [];
    },
    category() {
      return this.product.category;
    },
  },
  methods: {
    ...mapActions([
      'addProductToCart',
    ]),
    loadProduct() {
      this.productLoading = true;

      axios
        .get(API_BASE_URL + '/api/products/' + this.$route.params.id)
        .then((response) => this.productData = response.data)
        .catch(() => this.productLoadingFailed = true)
        .finally(() => this.productLoading = false);
    },
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.$store.state.cartLoading = true;
      this.formError = '';

      this.addProductToCart({
        productId: this.product.id,
        colorId: this.currentColor,
        sizeId: this.currentSize,
        quantity: this.productAmount,
      })
        .then(() => {
          this.productAdded = true;

          setTimeout(() => {
            this.productAdded = false;
          }, 1500);
        })
        .catch(() => {
          this.productAdded = false;
          this.formError = 'Не выбран размер товара';
        })
        .finally(() => {
          this.productAddSending = false;
          this.$store.state.cartLoading = false;
        });
    },
    updateInput(value) {
      this.productAmount = value;
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct({ productId: this.$route.params.id });
      },
      immediate: true
    },
    product: {
      handler() {
        this.currentColor = this.selectColor ? this.selectColor.color.id : this.product.colors[0].color.id;
        this.currentImage = this.selectColor
          ? this.selectColor.gallery[0].file.url
          : this.product.colors[0].gallery[0].file.url;
      },
    },
    currentColor: {
      handler(value) {
        if (value.color) this.currentColor = value.color.id;
        if (typeof value.gallery !== 'undefined') {
          if (value.gallery) {
            this.currentImage = value.gallery[0].file.url;
          } else {
            this.currentImage = notFoundPhoto;
          }
        }
      },
    },
    currentImage: {
      handler(value) {
        if (value.color) this.currentColor = value.color.id;
        if (typeof value.gallery !== 'undefined') {
          if (value.gallery) {
            this.currentImage = value.gallery[0].file.url;
          } else {
            this.currentImage = notFoundPhoto;
          }
        }
      },
    },
  },
};
</script>

<style scoped>
.item__row--center {
  margin-bottom: 30px;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.form__message--success {
  color: green;
}

.form__message--error {
  color: #e02d71;
}
</style>