<template>
  <li class="catalog__item">

    <router-link class="catalog__pic" :to="{ name: 'product', params: ({ id: product.id, selectColor: currentColor }) }">
      <img :src="currentImage" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>

    <ProductColors :colors="product.colors" :currentColor.sync="currentColor" />
  </li>
</template>

<script>
import ProductColors from '@/components/ProductColors.vue';
import notFoundPhoto from '@/assets/img/not-found-photo.png';
import numberFormat from '@/helpers/numberFormat.js';

export default {
  components: { ProductColors },
  props: ['product'],
  data() {
    return {
      currentColor: this.product.colors[0],
      currentImage: this.product.colors[0].gallery
        ? this.product.colors[0].gallery[0].file.url
        : notFoundPhoto,
    }
  },
  filters: {
    numberFormat,
  },
  watch: {
    currentColor: {
      handler(value) {
        if (typeof value.color !== 'undefined') this.currentColor = value;
        if (typeof value.gallery !== 'undefined') {
          if (value.gallery !== null) {
            this.currentImage = value.gallery[0].file.url;
          } else {
            this.currentImage = notFoundPhoto;
          }
        }
      },
    },
  },
};
</script>