<template>
  <main class="content container">
    <BasePreloader v-if="cartLoading" />

    <div v-if="!cartLoading">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Корзина
            </a>
          </li>
        </ul>

        <div class="content__row">
          <h1 class="content__title">
            Корзина
          </h1>
          <span class="content__info">
            {{ products.length }} товара
          </span>
        </div>
      </div>
      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list">
              <CartItem v-for="item in products" :key="item.id" :item="item" />
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
              Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
            </p>
            <p class="cart__price">
              Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
            </p>

            <router-link v-if="products.length >= 1" :to="{ name: 'order' }" class="cart__button button button--primery"
              type="submit">
              Оформить заказ
            </router-link>
          </div>
        </form>
      </section>
    </div>
  </main>
</template>

<script>
import BasePreloader from '@/components/BasePreloader.vue';
import CartItem from '@/components/CartItem.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters, mapState } from 'vuex';

export default {
  components: { CartItem, BasePreloader },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapState(['cartLoading']),
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
    }),
  },
}
</script>

<style>
.cart__button {
  text-align: center;
}
</style>