<template>
  <div class="item__desc">
    <ul class="tabs">
      <li class="tabs__item" v-for="tab in tabs" :key="tab.name">
        <a class="tabs__link" @click="selectTab(tab)" :class="{ 'tabs__link--current': tab.isActive }">
          {{ tab.name }}
        </a>
      </li>
    </ul>

    <div class="item__content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return { tabs: [] };
  },
  created() {
    this.tabs = this.$children;
  },
  methods: {
    selectTab(selectedTab) {
      this.tabs.forEach(tab => {
        tab.isActive = (tab.name == selectedTab.name);
      });
    },
  },
}
</script>

<style>
.tabs__link:not(.tabs__link--current) {
  cursor: pointer;
}
</style>