<template>
  <ul class="check-list">
    <li class="check-list__item" v-for="item in items" :key="item.id">
      <label class="check-list__label" v-if="item.productsCount > 0">
        <input class="check-list__check sr-only" type="checkbox" :value="item.id" v-model="computedItems">
        <span class="check-list__desc">
          {{ item.title }}
          <span>({{ item.productsCount }})</span>
        </span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  props: ['items', 'checkedItems'],
  computed: {
    computedItems: {
      get() {
        return this.checkedItems;
      },
      set(value) {
        this.$emit('update:checkedItems', value);
      },
    },
  },
}
</script>
