<template>
  <li class="cart__item product">
    <router-link :to="{ name: 'product', params: ({ id: item.product.id }) }" class="product__pic">
      <img :src="item.color.gallery[0].file.url" width="120" height="120" :alt="item.product.title">
    </router-link>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ backgroundColor: item.color.color.code }"></i>
        {{ item.color.color.title }}
      </span>
      Размер: {{ item.size.title }}
    </p>
    <span class="product__code">
      Артикул: {{ item.id }}
    </span>

    <BaseCounter :amount="quantity" @updateInput="updateInput" />

    <b class="product__price">
      {{ (item.quantity * item.product.price) | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.id)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import BaseCounter from './BaseCounter.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapActions, mapMutations } from 'vuex';

export default {
  props: ['item'],
  components: { BaseCounter },
  filters: { numberFormat },
  data() {
    return {
      productDeleted: false,
      productDeletedSending: false,
    }
  },
  computed: {
    quantity: {
      get() {
        return this.item.quantity;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          basketItemId: this.item.id,
          quantity: value,
        });
      }
    }
  },
  methods: {
    ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
    ...mapActions(['deleteProductFromCart']),

    updateInput(value) {
      this.quantity = value;
    },
    deleteProduct() {
      this.productDeleted = false;
      this.productDeletedSending = true;

      this.deleteProductFromCart({ basketItemId: this.item.id })
        .then(() => {
          this.productDeleted = true;
          this.productDeletedSending = false;
        });
    }
  },
}
</script>

<style scoped>
.product__info--color span {
  margin-right: 10px;
}

.product__info--color span i {
  outline: 1px solid #ededed;
}
</style>