<template>
  <main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ countProducts }} товара
        </span>
        <span class="content__info">
          Товаров на странице:
          <label class="form__label form__label--select form__label--page">
            <select class="form__select form__select--page" type="text" name="productsPerPage"
              v-model.number="productsPerPage">
              <option value="9">9</option>
              <option value="18">18</option>
              <option value="27">27</option>
            </select>
          </label>
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <aside class="filter">
        <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
          :category-id.sync="filterCategoryId" :color-ids.sync="filterColorIds" :material-ids.sync="filterMaterialIds"
          :season-ids.sync="filterSeasonIds" :select-category-id="selectCategoryId" />
      </aside>

      <section class="catalog">
        <BasePreloader v-if="productsLoading" />

        <div v-if="productsLoadingFailed">
          <h2>Произошла ошибка при загрузке товаров</h2>
          <button class="button button--primery button--reload" @click.prevent="loadProducts">
            Попробовать еще раз
          </button>
        </div>

        <ProductList v-else :products="products" :class="{ 'hidden': productsLoading }" />

        <BasePagination v-if="countProducts > productsPerPage" v-model="page" :count="countProducts"
          :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import BasePreloader from '@/components/BasePreloader.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config.js';

export default {
  components: { ProductList, BasePagination, ProductFilter, BasePreloader },
  props: ['selectCategoryId'],
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: this.selectCategoryId ? this.selectCategoryId : 0,
      filterColorIds: [],
      filterMaterialIds: [],
      filterSeasonIds: [],

      page: 1,
      productsPerPage: 9,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    }
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;

      axios
        .get(API_BASE_URL + '/api/products', {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorIds: this.filterColorIds,
            seasonIds: this.filterSeasonIds,
            materialIds: this.filterMaterialIds,
          }
        })
        .then((response) => this.productsData = response.data)
        .catch(() => {
          this.productsLoadingFailed = true;
        })
        .then(() => this.productsLoading = false);
    }
  },
  watch: {
    page() {
      this.loadProducts();
    },
    productsPerPage() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorIds() {
      this.loadProducts();
    },
    filterMaterialIds() {
      this.loadProducts();
    },
    filterSeasonIds() {
      this.loadProducts();
    }
  },
  created() {
    this.loadProducts();
  },
}
</script>

<style scoped>
.preloader {
  top: calc(40% - 50px);
}

.catalog {
  position: relative;
}

.content__info:last-child {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
}

.form__label {
  width: 50px;
}

.form__select--page {
  padding: 10px 15px 10px 10px;
  height: auto;
}

.form__label--page::after {
  right: 5px;
}
</style>