import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config.js';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],

    userAccessKey: null,

    cartProductsData: [],
    cartLoading: false,

    orderData: null,
    orderLoading: false,
  },
  mutations: {
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { basketItemId, quantity }) {
      const item = state.cartProducts.find(item => item.basketItemId === basketItemId);

      if (item) {
        item.quantity = quantity;
      }
    },
    deleteCartProduct(state, basketItemId) {
      state.cartProducts = state.cartProducts.filter(item => item.basketItemId !== basketItemId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          id: item.id,
          product: item.product,
          color: item.color,
          size: item.size,
          quantity: item.quantity,
        }
      });
    },
    updateСartLoading(state, cartLoading) {
      state.cartLoading = cartLoading;
    },
    updateOrderData(state, orderData) {
      state.orderData = orderData;
    },
    updateOrderLoading(state, orderLoading) {
      state.orderLoading = orderLoading;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts;
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.quantity) + acc, 0);
    },
  },
  actions: {
    loadCart(context) {
      context.commit('updateСartLoading', true);

      return axios
        .get(API_BASE_URL + '/api/baskets', {
          params: {
            userAccessKey: context.state.userAccessKey,
          }
        })
        .then(response => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }

          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
          context.commit('updateСartLoading', false);
        });
    },
    addProductToCart(context, { productId, colorId, sizeId, quantity }) {
      return axios
        .post(API_BASE_URL + '/api/baskets/products', {
          productId,
          colorId,
          sizeId,
          quantity,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        }
        )
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    updateCartProductAmount(context, { basketItemId, quantity }) {
      context.commit('updateCartProductAmount', { basketItemId, quantity });

      if (quantity < 1) return;

      return axios
        .put(API_BASE_URL + '/api/baskets/products', {
          basketItemId,
          quantity,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          }
        }
        )
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteProductFromCart(context, { basketItemId }) {
      context.commit('deleteCartProduct', { basketItemId });
      
      return axios
        .delete(API_BASE_URL + '/api/baskets/products', {
          data: { basketItemId },
          params: { userAccessKey: context.state.userAccessKey },
        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
        .catch(() => {
        });
    },
    loadOrderData(context, orderId) {
      context.commit('updateOrderLoading', true);

      return (new Promise(resolve => setTimeout(resolve, 1000)))
        .then(() => {
          axios
            .get(API_BASE_URL + '/api/orders/' + orderId, {
              params: {
                userAccessKey: context.state.userAccessKey,
              }
            })
            .then((response) => {
              context.commit('updateOrderData', response.data);
              context.commit('updateOrderLoading', false);
            });
        });
    },
  },
});